import { NgClass } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { RouterModule } from '@angular/router';
import { MainBarTitleService, TooltipComponent } from '@front-shared-lib';
import { Ipaginator } from '@main-shared-lib';
import { AdminListDropdownComponent } from '../admin-dropdown/admin-list-dropdown.component';
import { AdminPaginatorComponent } from '../admin-paginator/admin-paginator.component';
import { AdminTableFieldComponent } from '../admin-table-field/admin-table-field.component';

@Component({
  selector: 'app-admin-table',
  standalone: true,
  imports: [
    RouterModule,
    AdminPaginatorComponent,
    AdminListDropdownComponent,
    AdminTableFieldComponent,
    NgClass,
    TooltipComponent
  ],
  templateUrl: './admin-table.component.html',
  styleUrl: './admin-table.component.scss'
})
export class AdminTableComponent {
  @Input({ required: true }) currentActivePopupId?: string;
  @Input({ required: true }) selectable: boolean = true;
  @Input() loading?: boolean;
  @Input() limit?: number;
  @Input() offset = 0;
  @Input() count = 0;
  @Input() showingUpper?: number;
  @Input() showingLower?: number;
  @Input() currentIndex = 1;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  @Input({ required: true }) dataArr: any[] = [];
  @Input() selections: string[] = [];
  @Input({ required: true }) columns: {
    header: string;
    field: string;
    arrFields?: string[];
    sortable?: boolean,
    removeWhen?: string;
    isDefaultField?: boolean;
    useImage?: boolean;
    imageAccessor?: string;
    altLocalImage?: string;
    navUrlBase?: string;
    navUrlParamAccessor?: string;
    isMoneyValue?: boolean;
    isDateValue?: boolean;
    isBooleanValue?: boolean;
  }[] = [];

  @Input({ required: true }) actions: {
    label: string;
    icon: string;
    event: (item) => void
  }[] = [];


  @Output() paginateItems = new EventEmitter<Ipaginator>();
  @Output() sortChange = new EventEmitter<{ field: string; direction: 'asc' | 'desc' }>();
  @Output() selectionToggle = new EventEmitter<string[]>();
  @Output() openExtras = new EventEmitter<string>();
  @Output() viewItem = new EventEmitter();

  sortStates: { [key: string]: string } = {};

  activeHoverField = '';


  constructor(public mainBarTitle: MainBarTitleService) {}

  get loaderArray() {
    return Array.from({ length: this.limit || 10 });
  }

  onSort(column: string) {
    const currentDirection = this.sortStates[column] || 'asc';
    const newDirection = currentDirection === 'asc' ? 'desc' : 'asc';

    this.sortStates[column] = newDirection;
    this.sortChange.emit({ field: column, direction: newDirection });
  }

  toggleSelectAll() {
    if (!this.limit) {
      return;
    }

    if (this.selections && this.selections.length <= 0) {
      const startPos = this.limit * (this.currentIndex - 1);
      const endPos = startPos + this.limit;
      const selectedIds = this.dataArr.slice(startPos, endPos).map(item => item._id);

      this.selectionToggle.emit(selectedIds);
    } else {
      this.selectionToggle.emit([]);
    }
  }

  onHover(field: string) {
    if (this.dataArr.length === 0) {
      return;
    }
    this.activeHoverField = field;
  }

  onLeave() {
    this.activeHoverField = '';
  }
}
