/* eslint-disable @typescript-eslint/no-explicit-any */
import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Input } from '@angular/core';
import {
  CurrencyService, TruncateDotPipe
} from '@front-shared-lib';

@Component({
  selector: 'app-admin-table-field',
  standalone: true,
  imports: [
    DatePipe,
    CurrencyPipe,
    TruncateDotPipe
  ],
  templateUrl: './admin-table-field.component.html',
  styleUrl: './admin-table-field.component.scss'
})
export class AdminTableFieldComponent {
  @Input({ required: true }) field!: string;
  @Input({ required: true }) column!: any;
  @Input({ required: true }) item!: any;

  constructor(public currencyService: CurrencyService) {}

  getNestedProperty(obj, path: string) {
    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
    return path.split('.').reduce((o, key) => (o || {})[key], obj);
  }
}
